/* ###################  RESET ######################## */

* {
  padding: 0;
  margin: 0;
}

/* ###################  BASE STYLES ################## */
@import 'core/settings';

body {
  font-family: OpenSans, sans-serif;
  font-size: 16px;
  font-weight: normal;
  font-stretch: normal;
  font-style: normal;
  line-height: 1.5;
  letter-spacing: normal;
  text-align: center;
  color: #54585a;
}

.container {
  margin: 0 15px;
}

section.anchor {
  padding-top: 105px;
}

/* ###################  HEADER #################### */

.header-nav-link {
  color: white;
  text-decoration: none;
  cursor: pointer;
}

.heading {
  font-family: Rubik, sans-serif;
  font-size: 22px;
  font-weight: 500;
  font-stretch: normal;
  font-style: normal;
  line-height: 2.05;
  letter-spacing: normal;
  text-align: center;
  color: $color-green-primary;
}

.header {
  display: flex;
  flex-wrap: wrap;
  font-family: Rubik, sans-serif;
  justify-content: space-between;
  position: fixed;
  width: 100%;
  z-index: 999;
}

.header-base {
  display: flex;
  justify-content: center;
  align-items: center;
  opacity: 0.95;
  background-color: $color-green-primary;
  flex: 1;
  height: 60px;
}

.GroupName {
  line-height: 0.7;
  transform: scale(0.75);
}

.header-nav {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 45px;
  background-color: #14d9c4;
}

.header-nav-list {
  display: flex;
  justify-content: space-evenly;
  opacity: 0.9;
  width: 100%;
}

.header-nav-item {
  font-size: 15px;
  font-weight: 500;
  font-stretch: normal;
  font-style: normal;
  line-height: 2;
  letter-spacing: normal;
  color: #ffffff;
}

/* ###################  HERO #################### */

.main__hero {
  background: url("/assets/images/img-hero1.jpg") center top no-repeat;
  background-size: cover;
  height: 421px;
  position: relative;
}

.main__hero-text {
  position: absolute;
  color: black;
  font-family: Rubik, sans-serif;
  font-size: 18px;
  font-weight: 600;
  bottom: 0;
  background-color: #fff;
  width: 80%;
  left: 50%;
  transform: translate(-50%, 50%);
  padding: 14px 15px 16px 15px;
}

.image-main {
  background-size: cover;
  width: 360px;
  height: 200px;
}

/* ###################  TEAM #################### */

.team {
  position: relative;
  padding: 76px 15px 30px;
}

.team:after {
  content: "";
  width: 30px;
  height: 30px;
  background-color: #fff;
  position: absolute;
  left: 50%;
  top: 95%;
  transform: rotate(45deg);
}

.team .description {
  margin-bottom: 15px;
}

/* ###################  SKILLS ################## */

.skill-content {
  color: #54585a;
}

.skill {
  text-align: center;
  padding: 30px 20px 0 30px;
}

.skills {
  background-color: #b8b8b9;
  padding: 0px 15px 45px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.skill .heading {
  text-transform: uppercase;
}

.section--dark {
  background-color: #b8b8b9;
}

/* ##############  CONTAINER ALLS  ############## */

h3.name-adalaber {
  text-align: center;
}

ul>li {
  list-style: none;
}

.img-adalaber {
  width: 180px;
  height: 180px;
  margin: 30px auto 15px;
  display: block;
  text-align: center;
  border: 5px solid lightgrey;
  border-radius: 5px;
}

.social-icons {
  padding-top: 15px;
  padding-bottom: 15px;
}

.social-icons__item {
  color: $color-green-primary;
  display: inline;
  margin: 3px;
}

.social-icons__link {
  color: $color-green-primary;
  text-decoration: none;
  cursor: pointer;
}

.adalaber {
  padding: 15px;
}

/* ###################  FOOTER  #################### */

@import 'layout/footer';


/* ##############  RESPONSIVE  ############## */

@media all and (min-width: 768px) {
  .image {
    width: 115.9px;
    height: 45px;
    transform: translateY(-30%);
  }

  .anchor {
    padding-top: 60px;
  }

  .header {
    flex-wrap: nowrap;
  }

  .header-base {
    justify-content: flex-start;
    padding-left: 45px;
  }

  .header-nav {
    background-color: $color-green-primary;
    height: 60px;
    width: 50%;
  }

  .main__hero-text {
    opacity: 0.8;
    left: 6%;
    transform: none;
    width: 54%;
  }

  .team {
    padding: 45px;
    padding-bottom: 60px;
  }

  .skills {
    flex-direction: row;
    align-items: flex-start;
    padding: 45px 30px 60px;
  }

  .skill {
    flex-basis: 50%;
    padding-top: 0;
    margin: 0 15px;
  }

  .container-central {
    margin: 0 45px 0 45px;
  }

  .adalaber {
    display: flex;
    padding: 45px 0 60px 0;
  }

  h3.name-adalaber {
    text-align: left;
  }

  .container-bio-adalaber {
    margin: 0 15px;
    text-align: left;
  }

  .align-right>.container-bio-adalaber {
    text-align: right;
    padding-right: 45px;
  }

  .align-right>.container-bio-adalaber>h3.name-adalaber {
    text-align: right;
  }


  .img-adalaber {
    width: 205px;
  }

  .align-right>.adalaber-img {
    order: 1;
  }

}

