  .container-central {
    max-width: 924px;
    margin: auto;
  }

  .team {
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  .team p {
    width: 50%;
  }

  .skills {
    justify-content: center;
  }

  .skill {
